*{
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family:sans-serif;
}
.airnn--navbar {
    background-color: white;
    box-shadow: 4px 4px 4px rgb(234, 231, 231);
}
.airnn--logo {
    margin: auto;
    height: 110px;
    padding: 20px 30px;
}
.hero--banner {
    width: 75%;
    margin:0 auto;
    flex-direction:row;

}
.hero {
    padding: 10px;

}

.hero > h1 {
    margin:0;
    padding-left: 30px;
    display: block;
    
    
}
.hero > p {
    margin: 0;
    display: block;
    padding: 10px 30px;
    color: rgb(99, 98, 98);
}
.card {
    width: 275px;
    font-size: 12px;
    padding-left: 40px;
    margin:0 auto;
    float:left;
    position: relative;
}
.zefers--pic {
    width :100%;
    border-radius: 9px;
    
}

.card--stats {
    display:flex;
    align-items: center;
}

.card--star {
    height:14px;
}
span {
    margin: 0 auto;
}
.gray{
    color:rgb(188, 186, 186);
}
.bold{
    font-weight: bold;
}

.card--badge {

    position: absolute;
    top:10px;
    left:42px;
    background-color: white;
    padding:5px 7px;
    border-radius:2px;
    font-weight: bold;
}
